<template>
  <el-container class="paper-el-container">
    <el-header class="paper-el-header">
      <el-page-header @back="goBack" content="试卷管理"/>
    </el-header>
    <el-main class="paper-el-main">

      <el-container>
        <el-header>
          <paper-manage-head
          @cqueryClick="queryClick"
          @caddPaperClick="addPaperClick"
          @caddQuestion="addQuestion"
          :ccurrentView="currentView"/>
        </el-header>
        <el-main>
          <paper-msg-list
            :ccurrentPaperInfo="currentDirectionPaperInfo"
            ref="paperMsgList"
            @ceditPaperClick="editPaperClick"
            @cdeletePaperClick="deletePaperClick"
            v-show="currentView === 'queryPaperMsg'"/>
          
          <edit-paper-msg
          :ccurrentEditPaperInfo="currentEditPaperInfo"
          @ceditQuestionClick="editQuestionClick"
          @cdeleteQuestionClick="deleteQuestionClick"
          v-show="currentView === 'editPaperMsg'"
          style="height:58vh;"/>
          
          <edit-question-msg
          :ccurrentPaperMsg="currentPaperMsg"
          @cfinishEditQuestion="finishEditQuestion"
          v-show="currentView === 'editQuestionMsg'"
          ref="editQuestionMsg"/>

          <add-paper
          :ccurrentPaperMsg="currentPaperMsg"
          @caddNewPaper="addNewPaper"
          ref="addPaper"
          v-show="currentView === 'addPaper'"/>

          <add-question
          @caddNewQuestion="addNewQuestion"
          ref="addQuestion"
          v-show="currentView === 'addQuestion'"/>

        </el-main>
      </el-container>
      
 
    </el-main>
  </el-container>
</template>

<script>
  import axios from 'axios'
  import PaperManageHead from './childComps/PaperManageHead'
  import PaperMsgList from './childComps/PaperMsgList'
  import EditPaperMsg from './childComps/EditPaperMsg'
  import EditQuestionMsg from './childComps/EditQuestionMsg'
  import AddPaper from './childComps/AddPaper'
  import AddQuestion from './childComps/AddQuestion'
  export default {
  name: 'PaperManage',
  data() {
    return {
      currentView: '',
      currentDirectionPaperInfo: [],
      currentEditPaperInfo: {},
      currentPaperMsg:{},
    }
  },
  components: {
    PaperManageHead,
    PaperMsgList,
    EditPaperMsg,
    EditQuestionMsg,
    AddPaper,
    AddQuestion
  },
  props: {
    cquestionInfoList: {
      type: Array,
      default() {
        return []
      }
    }
  },
  methods: {
    goBack() {
      switch(this.currentView) {
        case 'queryPaperMsg':
          this.currentView = ''
          break;
        case 'editPaperMsg':
          this.currentView = 'queryPaperMsg'
          break;
        case 'addPaper':
          this.currentView = 'queryPaperMsg'
          break;
        case 'addQuestion':
          this.currentView = 'editPaperMsg'
          break;
        case 'editQuestionMsg':
          this.currentView = 'editPaperMsg'
          break;
        case '':
          this.$router.go(-1)
          break;
      }
    },
    queryClick(direction) {
      this.currentView = 'queryPaperMsg'
      this.$refs.paperMsgList.tableData = []
      // 获取当前考试类别的试卷内容
      this.getCurrentDirectionPaperInfo(direction)
      // 获取当前考题的考试类别
      this.currentPaperMsg.direction = direction
    },
    getCurrentDirectionPaperInfo(direction) {
      for(let i of this.cquestionInfoList) {
        if(i.direction === direction) {
          for(let j of i.paperInfo) {
            let tableInfo = {}
            tableInfo.courseId = j.courseId
            tableInfo.courseName = j.courseName
            // 给 paperMsgList tableData数据赋值
            this.$refs.paperMsgList.tableData.push(tableInfo)
          }
          this.currentDirectionPaperInfo = i.paperInfo
        }
      }
    },
    editPaperClick(e) {
      this.currentView = 'editPaperMsg'
      let courseId = ''
      e.target._prevClass === "el-icon-edit" ? courseId = e.path[3].textContent.substr(0, e.path[3].textContent.length - 7) : courseId = e.path[2].textContent.substr(0, e.path[2].textContent.length - 7)
      for(let i of this.currentDirectionPaperInfo) {
        if(i.courseId == courseId) {
          // 获取当前点击要更改的试卷相关信息
          this.currentEditPaperInfo = i
          // 获取当前考题的所属套题
          this.currentPaperMsg.courseName = this.currentEditPaperInfo.courseName
          this.$refs.addQuestion.currentPaperMsg = this.currentPaperMsg
        }
      }
    },
    deletePaperClick(e) {
      let courseId = ''
      let courseName = ''
      let isDone = false
      e.target._prevClass === "el-icon-delete" ? courseId = e.path[3].textContent.substr(0, e.path[3].textContent.length - 7) : courseId = e.path[2].textContent.substr(0, e.path[2].textContent.length - 7)
      e.target._prevClass === "el-icon-delete" ? courseName = e.path[3].textContent.substr(1, e.path[3].textContent.length - 1) : courseName = e.path[2].textContent.substr(1, e.path[2].textContent.length - 1)
      for(let i = 0; i < this.cquestionInfoList.length; i++) {
        if(this.cquestionInfoList[i].direction === this.currentPaperMsg.direction) {
          for(let j = 0; j < this.cquestionInfoList[i].paperInfo.length; j++) {
            if(!isDone && this.cquestionInfoList[i].paperInfo[j].courseName === courseName) {
              isDone = true
              this.cquestionInfoList[i].paperInfo.splice(j, 1)
              axios.put('http://localhost:3000/QuestionInfo/' + this.cquestionInfoList[i].id, this.cquestionInfoList[i])
                  .then(res => {
                    if(res.status === 200) {
                      this.$message({showClose: true, message: '试卷删除成功', type: 'success'})
                      this.currentView = ''
                    }
                  })
            }
          }
        }
      }
    },
    editQuestionClick(questionId) {
      this.currentView = 'editQuestionMsg'
      // 给 editQuestionMsg里的 currentQuestionMsg赋值, currentQuestionMsg保存着当前点击的试题信息
      for(let i of this.currentEditPaperInfo.choiceQuestion) {
        if(i.questionId === questionId) {
          this.$refs.editQuestionMsg.currentQuestionMsg = i
        }
      }
    },
    deleteQuestionClick(questionId) {
      // 完成编辑后更新 cquestionInfoList里的数据, 并且更新数据库内的相关信息
      for(let i = 0; i < this.cquestionInfoList.length; i++) {
        if(this.cquestionInfoList[i].direction === this.currentPaperMsg.direction) {
          for(let j of this.cquestionInfoList[i].paperInfo) {
            if(j.courseName === this.currentPaperMsg.courseName) {
              for(let k = 0; k < j.choiceQuestion.length; k++) {
                if(j.choiceQuestion[k].questionId === questionId) {
                  j.choiceQuestion.splice(k, 1)
                  axios.put('http://localhost:3000/QuestionInfo/' + this.cquestionInfoList[i].id, this.cquestionInfoList[i])
                  .then(res => {
                    if(res.status === 200) {
                      this.$message({showClose: true, message: '试题删除成功', type: 'success'})
                      this.currentView = 'editPaperMsg'
                    }
                  })
                }
              }
            }
          }
        }
      }
    },
    finishEditQuestion(val) {
      // 完成编辑后更新 cquestionInfoList里的数据, 并且更新数据库内的相关信息
      for(let i = 0; i < this.cquestionInfoList.length; i++) {
        if(this.cquestionInfoList[i].direction === this.currentPaperMsg.direction) {
          let id = i
          for(let j = 0; j < this.cquestionInfoList[i].paperInfo.length; j++) {
            if(this.cquestionInfoList[i].paperInfo[j].courseName === this.currentPaperMsg.courseName) {
              for(let l = 0; l < this.cquestionInfoList[i].paperInfo[j].choiceQuestion.length; l++) {
                if(this.cquestionInfoList[i].paperInfo[j].choiceQuestion[l].questionId === val.questionId) {
                  this.cquestionInfoList[i].paperInfo[j].choiceQuestion[l] = val
                  axios.put('http://localhost:3000/QuestionInfo/' + this.cquestionInfoList[i].id, this.cquestionInfoList[i])
                  .then(res => {
                    if(res.status === 200) this.$message({showClose: true, message: '试题修改成功', type: 'success'})
                    this.currentView = 'editPaperMsg'
                  })
                }
              }
            }
          }
        }
      }
    },
    addPaperClick() {
      this.currentView = 'addPaper'
      this.$refs.addPaper.currentDirection = this.currentPaperMsg.direction
    },
    addNewPaper(newPaperMsg) {
      let repeat = false
      for(let i = 0; i < this.cquestionInfoList.length; i++) {
        if(this.cquestionInfoList[i].direction === this.currentPaperMsg.direction) {
          // 假如试卷编号重复 提示添加失败
          for(let j = 0; j < this.cquestionInfoList[i].paperInfo.length; j++) {
            if(this.cquestionInfoList[i].paperInfo[j].courseId == newPaperMsg.courseId) {
              repeat = true
              this.$message({showClose: true, message: '添加失败: 试卷编号已存在', type: 'error'})
            }
          }
          // 假如试卷编号没重复, 给 cquestionInfoList数组推入新的试卷信息,并且更新数据库信息
          if(!repeat) {
            this.cquestionInfoList[i].paperInfo.push(newPaperMsg)
            axios.put('http://localhost:3000/QuestionInfo/' + this.cquestionInfoList[i].id, this.cquestionInfoList[i]).then(res => {
              this.$message({showClose: true, message: '试卷添加成功', type: 'success'})
              this.currentView = ''
              this.$refs.addPaper.currentPaperMsg = {courseId: '', courseName: '', choiceQuestion: [], answerQuestion: []}
            })
          }
        }
      }
    },
    addQuestion() {
      this.currentView = 'addQuestion'
    },
    addNewQuestion(newQuestionMsg) {
      let repeat = false
      let questionInfolistId = ''
      let paperInfoId = ''
      for(let i = 0; i < this.cquestionInfoList.length; i++) {
        if(this.cquestionInfoList[i].direction === this.currentPaperMsg.direction) {
          let questionInfolistId = i  
          for(let j = 0; j < this.cquestionInfoList[i].paperInfo.length; j++) {
            console.log(this.cquestionInfoList[i].paperInfo[j].courseName)
            console.log(this.currentPaperMsg.courseName)
            if(this.cquestionInfoList[i].paperInfo[j].courseName == this.currentPaperMsg.courseName) {
              paperInfoId = j   // 保存试题要添加的试卷ID
              // 假如 questionInfolistId 和 paperInfoId 都已经取到了值 并且该试卷不是一个空试卷
              if(paperInfoId !== undefined && this.cquestionInfoList[questionInfolistId].paperInfo[paperInfoId].choiceQuestion.length !== 0) {
                for(let k = 0; k < this.cquestionInfoList[questionInfolistId].paperInfo[paperInfoId].choiceQuestion.length; k++) {
                  if(repeat === false && this.cquestionInfoList[questionInfolistId].paperInfo[paperInfoId].choiceQuestion[k].questionId == newQuestionMsg.questionId) {
                    repeat = true
                    this.$message({showClose: true, message: '添加失败: 试题编号已存在', type: 'error'})
                  }
                }
                if(!repeat) {
                  this.cquestionInfoList[questionInfolistId].paperInfo[paperInfoId].choiceQuestion.push(newQuestionMsg)
                  axios.put('http://localhost:3000/QuestionInfo/' + this.cquestionInfoList[questionInfolistId].id, this.cquestionInfoList[questionInfolistId]).then(res => {
                    this.$message({showClose: true, message: '试题添加成功', type: 'success'})
                    this.currentView = 'editPaperMsg'
                    this.$refs.addQuestion.currentQuestionMsg = {question: '', chose: { A: '', B: '', C: '', D: '' }, answer: '', questionId: ''}
                  })
                }
              }
              if(paperInfoId !== undefined && this.cquestionInfoList[questionInfolistId].paperInfo[paperInfoId].choiceQuestion.length === 0) {
                this.cquestionInfoList[questionInfolistId].paperInfo[paperInfoId].choiceQuestion.push(newQuestionMsg)
                axios.put('http://localhost:3000/QuestionInfo/' + this.cquestionInfoList[questionInfolistId].id, this.cquestionInfoList[questionInfolistId]).then(res => {
                  this.$message({showClose: true, message: '试题添加成功', type: 'success'})
                  this.currentView = 'editPaperMsg'
                  this.$refs.addQuestion.currentQuestionMsg = {question: '', chose: { A: '', B: '', C: '', D: '' }, answer: '', questionId: ''}
                })
              }
            }
          }
        }
      }
    }
  }
}
</script>

<style>
  .paper-el-main {
    margin: 0 32px;
  }
</style>