<template>
  <el-container class="paper-msg-list">
    <table>
      <thead>
        <th>试卷编号</th>
        <th>所属套题</th>
        <th>操作</th>
      </thead>
      <tbody>
        <tr v-for="(item, index) in this.tableData" :key="index">
          <td>{{item.courseId}}</td>
          <td>{{item.courseName}}</td>
          <td>
            <!-- 编辑按钮 -->
            <el-button 
            type="primary" 
            icon="el-icon-edit" 
            circle 
            @click="editClick($event)"/>
            <!-- 删除按钮 -->
            <el-button 
            type="danger" 
            icon="el-icon-delete" 
            circle 
            @click="deleteClick($event)"/>
          </td>
        </tr>
      </tbody>
    </table>
  </el-container>
</template>
 
<script>
  export default {
  name: 'PaperMsgList',
  data() {
    return {
      tableData: []
    }
  },
  components: {
  },
  props: {
    ccurrentPaperInfo: {
      type: Array,
      default() {
        return []
      }
    },
    ccurrentTableInfo: {
      type: Array,
      default() {
        return []
      }
    }
  },
  methods: {
    editClick(e) {
      this.$emit('ceditPaperClick', e)
    },
    deleteClick(e) {
      this.$emit('cdeletePaperClick', e)
    }
  }
}
</script>

<style>
  .paper-msg-list table {
    border-collapse: collapse;
    text-align: center;
  }
  .paper-msg-list th {
    padding: 21px 64px;
    border: 1px solid #000;
  }
  .paper-msg-list td {
    padding: 12px 32px;
    border: 1px solid #000;
  }
</style>