<template>
<el-container class="edit-paper-msg">
  <table>
    <tr v-for="(v, k) in this.ccurrentEditPaperInfo.choiceQuestion" :key="k">
      {{v.questionId}}、{{v.question}}
      <span>
        答案:&nbsp;{{v.answer}}
        <a href="#" @click="editQuestionClick(v.questionId)">编辑</a>&nbsp;
        <a href="#" @click="deleteQuestionClick(v.questionId)">删除</a>
      </span>
      <p>A、{{v.chose.A}}</p>
      <p>B、{{v.chose.B}}</p>
      <p>C、{{v.chose.C}}</p>
      <p>C、{{v.chose.D}}</p>
    </tr>
  </table>
</el-container>
</template>

<script>
export default {
  name: 'EditPaperMsg',
  data() {
    return {

    }
  },
  props: {
    ccurrentEditPaperInfo: {
      type: Object,
      default() {
        return {}
      }
    }
  },
  methods: {
    editQuestionClick(questionId) {
      this.$emit('ceditQuestionClick', questionId)
    },
    deleteQuestionClick(questionId) {
      this.$emit('cdeleteQuestionClick', questionId)
    }
  }
}
</script>

<style>
  .edit-paper-msg {
    line-height: 2;
  }
  .edit-paper-msg a {
    text-decoration: none;
    margin-left: 10px;
  }
  .edit-paper-msg p {
    text-indent: 2em;
  }
</style>