<template>
  <el-container class="add-question-msg">
    <el-header>
      <span style="font-size: 20px;margin: 0 106px;">当前的考题类别: {{currentPaperMsg.direction}}， 所属套题：{{currentPaperMsg.courseName}}</span>
    </el-header>
    <el-main>
      <el-form :model="currentQuestionMsg">
        <table>
          <tr>
            <td class="add-question-title">试题编号</td>
            <td>
              <el-form-item>
                <input type="text" placeholder="请输入试题编号" v-model="currentQuestionMsg.questionId"/>
              </el-form-item>
            </td>
          </tr>
          <tr>
            <td class="add-question-title">试题内容</td>
            <td>
              <el-form-item>
                <input type="textarea"
                :autosize="{ minRows: 2, maxRows: 8}"
                placeholder="请输入内容" 
                v-model="currentQuestionMsg.question"/>
              </el-form-item>
            </td>
          </tr>
          <tr>
            <td class="add-question-title">选择</td>
            <td>
              <el-form-item>
                <input type="text" placeholder="请输入A选项" v-model="currentQuestionMsg.chose.A"/><br/>
                <input type="text" placeholder="请输入B选项" v-model="currentQuestionMsg.chose.B"/><br/>
                <input type="text" placeholder="请输入C选项" v-model="currentQuestionMsg.chose.C"/><br/>
                <input type="text" placeholder="请输入D选项" v-model="currentQuestionMsg.chose.D"/>
              </el-form-item>
            </td>
          </tr>
          <tr>
            <td class="add-question-title">答案</td>
            <td>
              <el-form-item>
                <input type="text" placeholder="请输入答案" v-model="currentQuestionMsg.answer"/>
              </el-form-item>
            </td>
          </tr>
        </table>
      </el-form>
      <el-button round class="add-question-button" @click="addQuestion">添加试卷</el-button>
    </el-main>
  </el-container>
</template>

<script>
export default {
  name: 'AddQuestion',
  data() {
    return {
      currentPaperMsg: {},
      currentQuestionMsg: {
        question: '',
        chose: {
          A: '',
          B: '',
          C: '',
          D: ''
        },
        answer: '',
        questionId: ''
      }
    }
  },
  methods: {
    addQuestion() {
      this.currentQuestionMsg.questionId = parseInt(this.currentQuestionMsg.questionId)
      this.$emit('caddNewQuestion', this.currentQuestionMsg)
    }
  }
}
</script>

<style>
  .add-question-button {
    margin: 32px 267px;
  }
  .add-question-msg table {
    border-collapse: collapse;
  }
  .add-question-msg td {
    padding: 16px 12px;
    border: 1px solid #000;
  }
  .add-question-msg input {
    width: 500px;
    border: none;
    border-radius: 3px;
    margin-top: 20px;
    padding: 12px;
  }
  .add-question-title {
    width: 120px;
    text-align: center;
  }
</style>