<template>
  <el-container class="edit-question-msg">
    <el-header>
      <span style="font-size: 20px;margin: 0 98.5px;">当前的考题类别: {{ccurrentPaperMsg.direction}}，所属套题: {{ccurrentPaperMsg.courseName}}</span>
    </el-header>
    <el-main>
      <el-form :model="currentQuestionMsg">

        <table>
          <tr>
            <td>试题内容</td>
            <td>
              <el-form-item prop="question">
                <el-input
                type="textarea"
                :autosize="{ minRows: 2, maxRows: 8}"
                placeholder="请输入内容"
                :value="currentQuestionMsg.question"
                v-model="currentQuestionMsg.question"/>
              </el-form-item>
            </td>
          </tr>
          <tr>
            <td>试题选项</td>
            <td>
              <el-form-item prop="chose">
                A、<input type="text" placeholder="请输入试题选项" v-model="currentQuestionMsg.chose.A"/><br/>
                B、<input type="text" placeholder="请输入试题选项" v-model="currentQuestionMsg.chose.B"/><br/>
                C、<input type="text" placeholder="请输入试题选项" v-model="currentQuestionMsg.chose.C"/><br/>
                D、<input type="text" placeholder="请输入试题选项" v-model="currentQuestionMsg.chose.D"/>
              </el-form-item>
            </td>
          </tr>
          <tr>
            <td>试题答案</td>
            <td>
              <el-form-item prop="answer">
                <input 
                style="width: 100px; float:left" 
                type="text" 
                placeholder="请输入试题答案" 
                v-model="currentQuestionMsg.answer">
              </el-form-item>
            </td>
          </tr>
        </table>
      </el-form>
      <el-button round class="finish-edit-button" @click="finishEdit">完成编辑</el-button>
    </el-main>
  </el-container>
</template>

<script>
export default {
  name: 'EditQuestionMsg',
  data() {
    return {
      currentQuestionMsg: {
        questionId: '',
        question: '',
        chose: {
          A: '',
          B: '',
          C: '',
          D: ''
        },
        answer: ''
      }
    }
  },
  props: {
    ccurrentPaperMsg: {
      type: Object,
      default() {
        return {}
      }
    }
  },
  methods: {
    finishEdit() {
      this.$emit('cfinishEditQuestion', this.currentQuestionMsg)
    }
  },
  created() {
  }
}
</script>

<style>
  .edit-question-msg {
    margin: 0 32px;
  }
  .edit-question-msg table {
    border-collapse: collapse;
    text-align: center;
  }
  .edit-question-msg td {
    padding: 16px 12px;
    border: 1px solid #000;
  }
  .edit-question-msg input {
    width: 500px;
    border: none;
    border-radius: 3px;
    margin-top: 20px;
    padding: 12px;
  }
  .finish-edit-button {
    margin: 32px 279.5px;
  }
</style>