<template>
  <el-container class="edit-question-msg">
    <el-header>
      <span style="font-size: 20px;margin: 0 193.5px;">当前的考题类别: {{currentDirection}}</span>
    </el-header>
    <el-main>
      <el-form :model="currentPaperMsg">

        <table>
          <tr>
            <td>试卷编号</td>
            <td>
              <el-form-item>
                <input type="text" placeholder="请输入试卷编号" v-model="currentPaperMsg.courseId"/>
              </el-form-item>
            </td>
          </tr>
          <tr>
            <td>所属套题</td>
            <td>
              <el-form-item>
                <input type="text" placeholder="请输入所属套题" v-model="currentPaperMsg.courseName"/>
              </el-form-item>
            </td>
          </tr>
        </table>
      </el-form>
      <el-button round class="add-paper-button" @click="addPaper">添加试卷</el-button>
    </el-main>
  </el-container>
</template>

<script>
export default {
  name: 'AddPaper',
  data() {
    return {
      currentDirection: '',
      currentPaperMsg: {
        courseId: '',
        courseName: '',
        choiceQuestion: [],
        answerQuestion: []
      }
    }
  },
  props: {
    ccurrentPaperMsg: {
      type: Object,
      default() {
        return {}
      }
    }
  },
  methods: {
    addPaper() {
      this.$emit('caddNewPaper', this.currentPaperMsg)
    }
  }
}
</script>

<style>
  .add-paper-button {
    margin: 32px 267px;
  }
</style>